import * as React from 'react';
import ListItem from '../../components/ListItem';
import TabMenu from '../../components/TabMenu';
import './Index.scss';

class User extends React.Component {
  state = {  };
  render() {
    return (
      <div className="user">
        <div className="top">
          <div className="t-operator">
            <i className="icon icon-setting" />
            <i className="icon icon-message"/>
          </div>
          <div className="info">
            <div className="avatar" style={{ backgroundImage: `url(source/avatar.jpeg)`, }}/>
            <div className="detail">
              <div className="name"></div>
              <div>
                <span className="user-fellow"> 100</span>
                <span className="user-fans"> 10</span>
              </div>
              <div className="level">
                <i className="icon-level"/>
                
              </div>
            </div>
          </div>
        </div>

        <div className="content">
          <ListItem title="" extra="2" href="#" />
          <ListItem title="" extra="64" href="#" />
        </div>
        <TabMenu current={'user'}/>
      </div>
    );
  }
}

export default User;



